import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import wutil from '../../lib/wifet-util';
import LineGraph from '../../components/lineGraph';

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore, windmillStore, weatherYrStore, weatherAiStore, productionStore, demandStore, priceStore } = this.props.stores;
		const { currentWindparkName } = windmillStore;

        appState.getFingerprint();
        await userStore.getInfo();

		const xMin = wutil.getDateDiffTime(-4);
		const xMax = wutil.getDateDiffTime(10);
		const now = Math.floor(new Date().getTime() / 1000);
		this.setState({ now, xMin, xMax });

		await this.loadYrData();
		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);

		weatherAiStore.generateData(forcastYrData);
		priceStore.generateData();
		demandStore.generateData();

		const { prices } = priceStore;
		productionStore.generateData(forcastYrData, prices);
    }

	async loadYrData() {
		const { weatherYrStore, windmillStore } = this.props.stores;
		const { windparks } = windmillStore;

		const yrPromises = [];
		for (let i = 0, l = windparks.length; i < l; i += 1) {
			const park = windparks[i];
			weatherYrStore.updateQueryFilter({
				name: park.name,
				altitude: park.position.altitude,
				lat: park.position.lat,
				lon: park.position.lon,
			});
			await weatherYrStore.load();
		}
		return await Promise.all(yrPromises);
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	render() {
		const { now, xMin, xMax } = this.state;
		const { windmillStore, weatherYrStore, weatherAiStore, productionStore, demandStore, priceStore } = this.props.stores;
		const { windparks, currentWindparkName } = windmillStore;
		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);
		const { forcasts: weatherAiData } = weatherAiStore;
		const { productionData, productionBoostData, productionLongLifeData, productionDataNOK } = productionStore;
		const { demands: demandData } = demandStore;
		const { prices: priceData } = priceStore;

		return (
			<div class='container-fluid mt-5 mb-5 pt-2'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fab fa-hubspot' /> Home</h1>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
					<div class='col-12'>
						<div class='d-flex'>
							<div class='p-2 flex-grow-1 bg-light mr-2'>
								<i class='fas fa-chart-bar' /> Production
								<LineGraph
									stores={this.props.stores}
									width={1200}
									height={200}
									showXTicks={1}
									xTicksVal={val => util.isoDate(val)}
									xMin={xMin}
									xMax={xMax}
									paddingTop={30}

									dataLeft={[productionData, productionBoostData, productionLongLifeData]}
									legendLeft={['Normal production', 'Boost 10%', 'Long life -10%']}
									yMaxLeft={500}
									yMinLeft={0}
									showYTicksLeft={1}
									yTicksLeft={['335Mw', '400Mw', '300Mw', '200Mw', '100Mw', '0Mw']}
									unitLeft={['Mw', 'Mw', 'Mw']}
									avgWindowLeft={[5, 5, 5]}

									dataRight={[productionDataNOK]}
									legendRight={['Earnings']}
									showYTicksRight={1}
									unitRight={['/h']}
									hoverValRight={[val => util.format((val / 100) * 3600, 2, ',', ' ', true)]}
									yTicksRightVal={val => util.format((val / 100) * 3600, 2, ',', ' ', true)}

									marker={now}
								/>
							</div>
							{/* <div class='p-2 bg-light'>
								<i class='fas fa-battery-three-quarters' /> Wear status
							</div> */}
						</div>

						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-cloud-sun-rain' /> Weather
								<LineGraph
									stores={this.props.stores}
									width={1200}
									height={200}
									showXTicks={1}
									xMin={xMin}
									xMax={xMax}
									xTicksVal={val => util.isoDate(val)}
									paddingTop={30}

									dataLeft={[forcastYrData, weatherAiData]}
									legendLeft={['Wind forcast Yr', 'Wind AI prediction']}
									yMaxLeft={30}
									yMinLeft={0}
									showYTicksLeft={1}
									yTicksLeft={['30m/s', '25m/s', '20m/s', '15m/s', '10m/s', '5m/s', '0m/s']}
									unitLeft={['m/s', 'm/s', 'm/s']}

									marker={now}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-chart-line' /> Electricity prices
								<LineGraph
									stores={this.props.stores}
									width={1200}
									height={200}
									showXTicks={1}
									xMin={xMin}
									xMax={xMax}
									xTicksVal={val => util.isoDate(val)}
									paddingTop={30}

									dataLeft={[priceData]}
									legendLeft={['Price']}
									yMaxLeft={200}
									yMinLeft={0}
									showYTicksLeft={1}
									yTicksLeft={['2NOK', '1.5NOK', '1NOK', '0.5NOK', '0NOK']}
									unitLeft={['Ã¸re']}

									marker={now}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light'>
								<i class='fas fa-hand-holding-usd' /> Demand
								<LineGraph
									stores={this.props.stores}
									width={1200}
									height={200}
									showXTicks={1}
									xMin={xMin}
									xMax={xMax}
									xTicksVal={val => util.isoDate(val)}
									paddingTop={30}

									dataLeft={[demandData]}
									legendLeft={['Demand']}
									yMaxLeft={4000}
									yMinLeft={0}
									showYTicksLeft={1}
									yTicksLeft={['4TW', '3TW', '2TW', '1TW', '0TW']}
									unitLeft={['Mw']}

									marker={now}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
